import { Input } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { BankAccount } from 'src/app/Model/BankAccount';
import { Expense } from 'src/app/Model/Expense';
import { AlertService } from 'src/app/services/alert.service';
import { BankAccountsService } from 'src/app/services/BankAccounts/bankaccounts.service';
import { ExpenseService } from 'src/app/services/expense/expense.service';

@Component({
  selector: 'app-add-expense',
  templateUrl: './add-expense.component.html',
  styleUrls: ['./add-expense.component.css']
})
export class AddExpenseComponent implements OnInit {

@Input() ExpenseDataSubject:Subject<Expense>;

  form:FormGroup;
  form2:FormGroup;
  ExpenseId:number;
  isEditMode=false;
  loading=false;
  expenses:Expense[];
  banks:BankAccount[];
  isVisible=false;
  modalTitle="Create new Expense";
  showExpense:boolean;
  constructor(private fb:FormBuilder,
    private _notification:AlertService,
    private _ExpenseService:ExpenseService,
    private _BankService:BankAccountsService
    ) {
    this.form=fb.group({
      expenseId:[null],
      expenseName:[null,Validators.required],
      expenseDate:[null,Validators.required],
      expenseAmount:[null,Validators.required],
      transactions:fb.array([
        fb.group({
          bankId:[null],
          amount:[null],
          transactionDate:[null],
          transactionType:2
        })
      ])
    });
    this.form2=fb.group({
      expenseName:[null,Validators.required],
    });
   }

   public get f() {return this.form.controls}
   public get controls(){return ((this.form.controls.transactions as FormArray).controls[0] as FormGroup).controls}

  ngOnInit(): void {

    this.form.controls.expenseAmount.valueChanges.subscribe(res=>{
      this.controls.amount.setValue(res);
    });
    this.form.controls.expenseDate.valueChanges.subscribe(res=>{
      this.controls.transactionDate.setValue(res);
    });
    this._BankService.bankAccountsObserver$.subscribe(res=>{
      this.banks=res;
    });
    this._BankService.GetBankAccounts();
    this.ExpenseDataSubject.subscribe(res=>{
      if(res)
      {
        this.form.patchValue(res);
        this.ExpenseId=res.id;
        this.isEditMode=true;
      }
      else
        {
          this.form.reset();
          this.ExpenseId=null;
          this.isEditMode=false;
        }
    });
    this._ExpenseService.expenseObserver$.subscribe(res=>{
      this.expenses=res;
    })
  }
  
  formatDate(res) {
    var date = new Date(`${res} UTC`);
    return date
  }
  startChange(event:any)
  {
    var date = this.formatDate(event.value);
    this.form.controls.expenseDate.setValue(date.toISOString());
   console.log("startChange",event.value)
  }
  handleModalOpen()
  {
    console.log('sadas')
    this.isVisible=true;
    this.modalTitle="New Expense";
    this.showExpense=false;
  }
  handleCancel()
  {
    this.isVisible=false;
    this.showExpense=false;
  }
  handleSubmit2()
  {
    let data = this.form2.value as Expense;
    this._ExpenseService.SaveExpenses(data).subscribe(res=>{
      if(res.isSuccessfull){
      
        this._ExpenseService.GetExpenses();
        this._notification.success('Expense Saved Successfully!');
      }
      else{
        this._notification.error('Error while saving Expense!');
      }
    })
  }
  handleSubmit(){

    this.loading=true;
    let data = this.form.value as Expense;
    data.transactions[0].transactionType=2;
    if(this.isEditMode){
      data.id=this.ExpenseId;
   
      this._ExpenseService.UpdateExpense(data).subscribe(res=>{
        this.loading=false;
        if(res.isSuccessfull){
 
          this._ExpenseService.GetExpenses();
          this._notification.success('Expense Updated Successfully!');
        }
        else{
          this._notification.error('Error while updating Expense!')
        }
      }); 

    }
    else{
      this.loading=false;
      this._ExpenseService.SaveExpenses(data).subscribe(res=>{
        if(res.isSuccessfull){
        
          this._ExpenseService.GetExpenses();
          this._notification.success('Expense Saved Successfully!');
        }
        else{
          this._notification.error('Error while saving Expense!');
        }
      })
    }

  }

}
