import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Expense } from 'src/app/Model/expense';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';

@Injectable({
  providedIn: 'root'
})
export class ExpenseService extends BaseApiHandlerService  {
  expenseObserver$:Observable<Expense[]>;
  private expenseSubject$: BehaviorSubject<Expense[]> = new BehaviorSubject<Expense[]>(undefined);

  public get expenses() {return this.expenseSubject$.value;}

  constructor(private http:HttpClient,Error)
  {
    super(http)
    this.expenseObserver$=this.expenseSubject$.asObservable();
  }SaveExpenses(data)
  {
    return this.Post(data,API_URL+API_ENDPOINTS.Expense);
  }
  UpdateExpense(data:Expense)
  {
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Expense)
  }
  GetExpenses()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Expense).subscribe(res=>{
      this.expenseSubject$.next(res.dynamicResult);
    });
  }

  DeleteExpense(id)
  {
    return this.Delete(id,API_URL+API_ENDPOINTS.Expense);
  }

}
